.projects__card {
    align-items: center;
    border-radius: var(--default-radius);
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin: var(--margin-24px);
    max-width: 400px;
    padding: 0.5rem 0;
    transition: all 0.3s ease-out;
}

.projects__card:hover {
    scale: 1.02;
    box-shadow: 0 1px 6px rgb(32 33 36 / 58%);
}

.projects__card > * {
    margin: 1rem 0;
}

.projects__technologies {
    display: flex;
    font-size: 2rem;
    justify-content: space-evenly;
    margin: 1rem var(--margin-24px);
    width: 80%;
}

h4 > * {
    margin: 0 1rem;
}